import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router)

const Home = () => import(/* webpackChunkName: "home" */ '@/pages/index');
const ImportExcel = () => import(/* webpackChunkName: "import" */ '@/pages/import');
const About = () => import(/* webpackChunkName: "about" */ '@/pages/about')
const Source = () => import(/* webpackChunkName: "source" */ '@/pages/source')
const Demo = () => import(/* webpackChunkName: "button" */ '@/pages/button')
const DataAnalysis = () => import(/* webpackChunkName: "data-analysis" */ '@/pages/data-analysis')
const CheckBox = () => import(/* webpackChunkName: "checkbox" */ '@/pages/checkbox')
export default new Router({
  routes: [
    { 
      path: '/', 
      name: 'home', 
      component: Home ,
      children: [
        {
          path: '/data-analysis', name: 'dataAnalysis', component: DataAnalysis
        }
      ]
    },
    { path: '/about', name: 'about', component: About },
    { path: '/source', name: 'Source', component: Source },
    { path: '/import', name: 'importExcel', component: ImportExcel },
    { path: '/demo', name: 'demo', component: Demo },
    { path: '/checkbox', name: 'checkBox', component: CheckBox },
  ]
})