import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export default new Vuex.Store({
    state:{
        lang:'cn',
        hidden:true,
        PM2d5: [], // excel数据
        CO: [], // excel数据
        NO2: [], // excel数据
        PM10: [], // excel数据
        SO2: [], // excel数据
        O3: [], // excel数据
        provinceAir:{
            PM2d5: [], // excel数据
            CO: [], // excel数据
            NO2: [], // excel数据
            PM10: [], // excel数据
            SO2: [], // excel数据
            O3: [], // excel数据            
        },
        policy: {
            GT: [], //淘汰钢铁产能 
            SN: [], //淘汰水泥产能 
            GL: [], //燃煤锅炉治理情况 
            HBC: [], //淘汰黄标车 
            PHBC: [], //淘汰黄标车(省份)
            OV: [], //加油站油气回收改造量 
            CC: [], //煤炭消费量 
            PG: [], //落后火电机组淘汰量 
            EI: [], //燃煤电厂超低排放改造量 
            EV: [], //新能源车推广量 
        },
        importantCity: [], // excel数据
        hotCity: [], // excel数据
        cityData: [], // excel数据
        chartData: {
            tab: 1, // 1: 空气质量 0: 政策进展
            area: 0, // 区域
            pollutant: 0, // 污染物
            policy: "GT", // 政策
            heatYear: 0, // 热力图年份
            barchartYear: [0,1,2,3,4], // 柱状图年份
            charType:0,//图形类型
        },
        
    },
    mutations:{
        changeFalse(state){//这里的state对应着上面这个state
            state.hidden = false
            //你还可以在这里执行其他的操作改变state
        },
        changeTrue(state){//这里的state对应着上面这个state
            state.hidden = true
            //你还可以在这里执行其他的操作改变state
        },
        changeLang(state,data){//这里的state对应着上面这个state
            state.lang = data
            //你还可以在这里执行其他的操作改变state
        },
        ['savePM2d5'](state, data){
            state.PM2d5 = data
        },
        ['saveCO'](state, data){
            state.CO = data
        },
        ['saveNO2'](state, data){
            state.NO2 = data
        },
        ['savePM10'](state, data){
            state.PM10 = data
        },
        ['saveSO2'](state, data){
            state.SO2 = data
        },
        ['saveO3'](state, data){
            state.O3 = data
        },
        //省份空气
        ['saveProvincePM2d5'](state, data){
            state.provinceAir.PM2d5 = data
        },
        ['saveProvinceCO'](state, data){
            state.provinceAir.CO = data
        },
        ['saveProvinceNO2'](state, data){
            state.provinceAir.NO2 = data
        },
        ['saveProvincePM10'](state, data){
            state.provinceAir.PM10 = data
        },
        ['saveProvinceSO2'](state, data){
            state.provinceAir.SO2 = data
        },
        ['saveProvinceO3'](state, data){
            state.provinceAir.O3 = data
        },
        ['save74city'](state, data){
            state.importantCity = data
        },
        ['saveHotcity'](state, data){
            state.hotCity = data
        },
        ['saveCityData'](state, data){
            state.cityData = data
        },
        ['UPDATE_TAB'](state, val){
            state.chartData.tab = val
        },
        ['UPDATE_AREA'](state, val){
            state.chartData.area = val
        },
        ['UPDATE_POLLUTANT'](state, val){
            state.chartData.pollutant = val
        },
        ['UPDATE_POLICY'](state, val){
            state.chartData.policy = val
        },
        ['UPDATE_HEATYEAR'](state, val){
            state.chartData.heatYear = val
        },
        ['UPDATE_BARCHARTESR'](state, val){
            state.chartData.barchartYear = val
        },
        ['UPDATE_CHARTYPE'](state, val){
            state.chartData.charType = val
        },
        ['saveGT'](state, data) {
            state.policy.GT = data;
        },
        ['saveSN'](state, data) {
            state.policy.SN = data;
        },
        ['saveGL'](state, data) {
            state.policy.GL = data;
        },
        ['saveHBC'](state, data) {
            state.policy.HBC = data;
        },
        ['saveCC'](state, data) {
            state.policy.CC = data;
        },
        ['savePG'](state, data) {
            state.policy.PG = data;
        },
        ['saveEI'](state, data) {
            state.policy.EI = data;
        },
        ['saveEV'](state, data) {
            state.policy.EV = data;
        },
        ['saveOV'](state, data) {
            state.policy.OV = data;
        },
        ['savePHBC'](state, data) {
            state.policy.PHBC = data;
        },
    },
    actions:{
        changeFalse(context){//这里的context和我们使用的$store拥有相同的对象和方法
            context.commit('changeFalse');
            //你还可以在这里触发其他的mutations方法
        },
        changeTrue(context){//这里的context和我们使用的$store拥有相同的对象和方法
            context.commit('changeTrue');
            //你还可以在这里触发其他的mutations方法
        },
    }
})